
#product {
  margin-top: 40px;
}
.product-roll-container {
  position: absolute;
  width: 100%;
  top: 40px;
  bottom: 0px;
}
.newslist-container {
    height: auto;
    padding-bottom: 48px;
}
.newslist {
    position: relative;
    height: 80px;
    padding-top: 20px;
    background: #fff;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.newslist .recommend-infor {
    padding-left: 12px;
}
.newslist .recommend-infor p {
    width: 70%;
    color: #000;
    line-height: 20px;
    padding-top: 2px;
    padding-bottom: 4px;
}
.newslist .recommend-infor span {
    font-size: 14px;
}
.newslist .recommend-infor img{
    position: absolute;
    height: 60px;
    width: 60px;
    right: 12px;
    top: 50%;
    margin-top: -30px;
}



