
.information {
  z-index: 1100;
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  bottom: 0;
  background: #efeff4;
}
.information-head {
  height: 40px;
  width: 100%;
  line-height: 40px;
  text-align: center;
  color: #000;
  background-color: #f5f5f7;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.information-head i {
  position: absolute;
  padding: 0 10px 0 10px;
  display: block;
  transform:rotate(180deg);
  color: #007AFF;
  font-size: 20px;
  font-weight: 600;
}
.information-img {
  width: 100%;
  height: 80px;
  line-height: 80px;
  background: #fff;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
  color: #000;
  padding-left: 10px;
}
.information-img span {
  float: right;
  display: block;
  height: 60px;
  width: 60px;
  margin-right: 20px;
  margin-top: 10px;
}
.information-img span img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.information-list {
  width: 100%;
  height: 50px;
  line-height: 50px;
  background: #fff;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
  color: #000;
  padding-left: 10px;
}
.information-list span {
  float: right;
  display: block;
  margin-right: 20px;
  color: #8b8b8b;
}
.bg {
  z-index: 1500;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  background-color: rgba(0,0,0,.4);
}
.camera-bg {
  z-index: 1600;
  position: fixed;
  bottom: 0;
  width: 100%;
}
.moveCamera-enter-active, .moveCamera-leave-active {
      transition: all 0.2s linear;
}
.moveCamera-enter, .moveCamera-leave-active {
  transform: translate3d(0, 100%, 0);
}
.camera-layer {
  z-index: 1500;
  position: fixed;
  bottom: 0px;
  width: 100%;
  height: 100%;
}
.camera-layer-top {
  margin: 0 15px 10px 15px;
}
.camera-layer-top ul li {
  display: block;
  width: 100%;
  background: #fff;
  text-align: center;
}
.select {
  height: 40px;
  line-height: 40px;
  font-size: 12px;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.photo {
  position: relative;
  height: 50px;
  line-height: 50px;
  font-size: 18px;
  color: #007AFF;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.photo-album {
  position: relative;
  height: 50px;
  line-height: 50px;
  font-size: 18px;
  color: #007AFF;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.camera-cancle {
  height: 50px;
  line-height: 50px;
  font-size: 18px;
  font-weight: 500;
  color: #007AFF;
  background: #fff;
  text-align: center;
  border-radius: 10px;
  margin: 0 15px 15px 15px;
}
.fileBtn {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
}
.file-select {
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  opacity: 0;
}



