
#my {
    -webkit-overflow-scrolling: touch;
}
.my_title {
    height: 40px;
    width: 100%;
    line-height: 40px;
    text-align: center;
    color: #000;
    /* border-bottom: 0.5px solid #A7A7AB; */
    background-color: #f5f5f7;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.my_header {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    height: 100px;
    width: 100%;
    margin-top: 20px;
    background: #fff;
}
.my_header .user_img {
    width: 50px;
    height: 50px;
    margin-left: 20px;
    margin-top: 25px;
}
.user_img img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}
.my_header .des {
    -ms-flex: 1;
        flex: 1;
    padding-left: 20px;
    padding-top: 30px;
    background: url('./right2.png') 92% center no-repeat;
    background-size: 4%;
}
.my_header .des p{
    font-size: 18px;
    line-height: 20px;
    font-weight: 500;
    color: #000;
    padding-bottom: 2px;
}
.my_header .des .user_work{
    font-size: 14px;
    font-weight: 400;
}
.my_header .des i{
    position: absolute;
    display: inline-block;
    right: 10px;
}
.my_items {
    width: 100%;
    background-color: #fff;
    margin-top: 20px;
}
.my_items ul li{
    display: block;
    height: 50px;
    line-height: 50px;
    font-size: 16px;
    color: #000;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.my_ic {
    color: #ABB4BF;
    font-size: 18px;
    padding-left: 20px;
    padding-right: 30px;
}
.my_set {
    width: 100%;
    background-color: #fff;
    margin-top: 20px;
}
.my_set ul li{
    display: block;
    height: 50px;
    line-height: 50px;
    font-size: 16px;
    color: #000;
}
.feedback {
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}



