
.listdetail {
  z-index: 1100;
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  background: #fff;
  transform: translate3d(0, 0, 0);
}
.move-enter-active, .move-leave-active {
    transition: all 0.2s linear;
}
.move-enter, .move-leave-active {
  transform: translate3d(100%, 0, 0);
}
.listdetail .detail-head {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 40px;
  line-height: 40px;
  background-color: #f5f5f7;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
  color: #000;
}
.detail-head .Btn-back {
  float: left;
  height: 100%;
  width: 40px;
  text-align: center;
}
.Btn-back i{
  transform:rotate(180deg);
  color: #007AFF;
  font-size: 20px;
  font-weight: 600;
}
.Btn-fenxiang {
  float: right;
  width: 60px;
  height: 100%;
  background: url(./fenxiang.png) center center no-repeat;
  background-size: 32%;
}
.author-infor {
  -ms-flex: 1;
      flex: 1;
}
.author-infor .author-img {
  float: left;
  width: 40px;
  height: 40px;
  line-height: 40px;
}
.author-infor .author-img img{
  display: block;
  width: 30px;
  height: 30px;
  margin-top: 5px;
  border-radius: 50%;
}
.author-infor  span {
  float: left;
  display: inline-block;
  width: 50px;
  font-size: 15px;
}
.detail-content .head-img {
  height: 180px;
  width: 100%;
}
.detail-content .head-img img{
  height: 100%;
  width: 100%;
}
.detail-content .content-read {
  color: #000;
  padding: 20px 15px 20px 15px;
}
.content-read .list-title {
  padding-bottom: 30px;
  font-size: 18px;
  line-height: 22px;
  font-weight: 600;
}
.main-read {
  font-size: 15px;
  line-height: 20px;
}
.foot {
  z-index: 1300;
  position: fixed;
  bottom: 0;
  left: 0;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 48px;
  line-height: 48px;
  background-color: #F5F5F5;
  box-shadow: inset 0px 1px 1px -1px #A7A7AB;
}
.foot .font-item {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
}
.foot .font-item i{
  font-size: 22px;
}
.foot .like-num {
  float: left;
  margin-right: 20px;
  width: 55%;
  font-size: 14px;
  text-align: right;
  height: 100%;
}



