
.frontend {
  height: auto;
  margin-top: 50px;
  overflow: hidden;
}
.frontend-roll-container {
  position: absolute;
  width: 100%;
  top: 40px;
  bottom: 10px;
}
.frontend-hot {
  width: 100%;
  height: auto;
  background: #fff;
  margin-bottom: 10px;
  margin-top: 10px;
}
.frontend-hot-head {
  width: 100%;
  height: 37px;
  line-height: 37px;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.frontend-hot-head .left {
  float: left;
  color: #007AFF;
  font-size: 15px;
  padding-left: 10px;
}
.frontend-hot-head .left i{
  padding-right: 5px;
  font-size: 15px;
}
.frontend-hot-head .right {
  float: right;
  padding-right: 10px;
}
.frontend-hot-head .right i {
  padding: 8px 5px 8px 10px;
  font-size: 20px;
  font-weight: 600;
  color: #E0E0E0;
}
.frontend-hot-list {
  position: relative;
  height: 100px;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.frontend-hot-list .recommend-infor {
  float: left;
  width: 70%;
  padding-top: 20px;
  padding-left: 12px;
}
.recommend-infor-ic {
  padding-top: 5px;
}
.recommend-infor-ic span {
  display: inline-block;
  height: 14px;
  line-height: 14px;
  padding-right: 5px;
  font-size: 10px;
  color: #E0E0E0;
}
.recommend-infor-ic span i {
  font-size: 14px;
}
.recommend-infor p {
  color: #000;
  line-height: 20px;
}
.frontend-hot-list .recommend-img {
  position: absolute;
  width: 60px;
  height: 60px;
  right: 12px;
  top: 20px;
}
.frontend-hot-list .recommend-img img {
  width: 60px;
  height: 60px;
}
.newslist-container {
  height: auto;
  padding-bottom: 48px;
}
.newslist {
  position: relative;
  height: 80px;
  padding-top: 20px;
  background: #fff;
  box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.newslist .recommend-infor {
  padding-left: 12px;
}
.newslist .recommend-infor p {
  width: 70%;
  color: #000;
  line-height: 20px;
  padding-top: 2px;
  padding-bottom: 4px;
}
.newslist .recommend-infor span {
  font-size: 14px;
}
.newslist .recommend-infor img{
  position: absolute;
  height: 60px;
  width: 60px;
  right: 12px;
  top: 50%;
  margin-top: -30px;
}



