
#find {
    overflow: hidden;
    padding-bottom: 48px;
}
.find-head {
    z-index: 1000;
    position: fixed;
    width: 100%;
    height: 40px;
    top: 0;
    background-color: #fff;
    text-align: center;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.find-roll-container {
    position: absolute;
    width: 100%;
    top: 40px;
    bottom: 200px;
}
.content-wrapper {
}
.search-container {
    width: 95%;
    height: 30px;
    padding-top: 5px;
    padding-left: 2.5%;
}
.searchBox {
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: #E0E1E5;
    border-radius: 5px;
}
.search_img {
    font-size: 20px;
}
.recommend {
    width: 100%;
    top: 40px;
    bottom: 0;
}
.recommend .recommend-content {
    height: 100%;
    overflow: hidden;
}
.recommend .recommend-content .slider-wrapper {
    position: relative;
    width: 100%;
    overflow: hidden;
}
.hot {
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    height: 90px;
    line-height: 100px;
    margin-top: 10px;
    background: #fff;
    color: #2E3135;
}
.hot .hot_item {
    -ms-flex: 1;
        flex: 1;
    text-align: center;
}
.hot_ic {
    display: block;
    height: 50px;
    padding-top: 13px;
}
.hot .hot_item span {
    display: block;
    height: 25px;
    line-height: 20px;
}
.week {
    background: url('./image/hot_1.png') center center no-repeat;
    background-size: 35%;
}
.collect {
    background: url('./image/hot_2.png') center center no-repeat;
    background-size: 35%;
}
.activity {
    background: url('./image/hot_3.png') center center no-repeat;
    background-size: 40%;
}
.zhuanlan {
    background: url('./image/hot_4.png') center center no-repeat;
    background-size: 30%;
}
.boil {
    height: 160px;
    width: 100%;
    margin-top: 10px;
    background-color: #fff;
}
.boil .boil_tit {
    height: 45px;
    line-height: 45px;
    color: #000;
    font-size: 15px;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.boil_tit_img {
    display: block;
    float: left;
    width: 24px;
    height: 100%;
    margin-left: 12px;
    margin-right: 5px;
    background: url('./image/boil_point.png') left center no-repeat;
    background-size: 70%;
}
.right_img {
    float: right;
    width: 18px;
    height: 100%;
    margin-right: 10px;
}
.boil .boil_img {
    width: auto;
    height: 120px;
    overflow-x: auto !important;
}
::-webkit-scrollbar {
    display: none;
}
.find-boil-container {
}
.boil .boil_img ul {
    height: 120px;
    width: 1055px;
    padding-left: 10px;
    overflow-x: auto;
    white-space: nowrap;
}
.boil .boil_img ul li {
    position: relative;
    display: inline-block;
    /* float: left; */
    height: 90px;
    width: 180px;
    border-radius: 5px;
    margin: 10px;
    margin-left: 0;
}
.boil .boil_img ul li span {
    position: absolute;
    display: block;
    width: 180px;
    height: 90px;
    border-radius: 5px;
    color: #fff;
    font-size: 15px;
    box-shadow: inset 180px 10px 5px rgba(0,0,0,.4);
}
.boil .boil_img ul li span p {
    width: 145px;
    height: 35px;
    padding-left: 20px;
    padding-right: 15px;
    padding-top: 25px;
    font-size: 15px;
    line-height: 18px;
    overflow: hidden;
    white-space: normal;
    text-overflow: ellipsis !important;
    display: -webkit-box;
    -webkit-line-clamp: 2;
}
.boil_img_1 {
    /* background-image: url(./image/boil_item_1.png); */
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: cover;
}
.boil_img_1 img{
    position: absolute;
    top: 0;
    left: 0;
    height: 90px;
    width: 180px;
    border-radius: 5px;
}
