.view_all {
    position: absolute;
    display: inline-block;
    height: 90px;
    line-height: 130px;
    text-align: center;
    width: 90px;
    margin-top: 10px;
    color: #53AAFF;
    font-size: 15px;
    border: 0.5px solid #ccc;
    border-radius: 5px;
    background: url(./image/right3.png) center 12px no-repeat;
    background-size: 40%;
}
.content {
    background-color: #FFF;
    margin-top: 10px;
}
.content .content_head {
    height: 45px;
    line-height: 45px;
    font-size: 15px;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.content .content_head .head_left {
    float: left;
    width: 30%;
    color: #000;
    padding-left: 40px;
    background: url(./image/hot_red.png) 12px center no-repeat;
    background-size: 11%;
}
.content .content_head .head_right {
    float: right;
    width: 30%;
    text-align: right;
    padding-right: 12px;
}
.content .content_head .head_right i{
    margin-right: 5px;
}
.content_list ul{
    display: block;
}
.content_list .cont_list_item {
    position: relative;
    display: block;
    height: 80px;
    padding-top: 20px;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.content_list .cont_list_item .list_item_container {
    padding-left: 12px;
}
.list_item_container p {
    width: 70%;
    color: #000;
    font-size: 16px;
    line-height: 18px;
    padding-top: 2px;
    padding-bottom: 10px;
}
.list_item_container span {
    font-size: 14px;
}
.content_list .cont_list_item  img {
    position: absolute;
    height: 60px;
    width: 60px;
    top: 50%;
    margin-top: -30px;
    right: 12px;
}
img[src=""] {
    opacity: 0;
}
.loading-container {
    position: absolute;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
}



