
.boildetail-roll-container {
  position: absolute;
  width: 100%;
  top: 0;
  bottom: 50px;
}

/* 滚动时header颜色变化 */
.bgActive {
  background-color: #f5f5f7;
}
.BackActive {
  color: #007AFF !important;
}
.authorActive {
  color: #000 !important;
}
.fenxActive {
  color: #ccc;
}
.boildetail-Btn-back-2 {
  color: #007AFF;
}
.boildetail {
  z-index: 1500;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  /* margin-bottom: 50px; */
  background: #efeff4;
  /* overflow-y: auto;
  -webkit-overflow-scrolling: touch; */
}
.boildetail-head-bg {
  position: relative;
  width: 100%;
  height: 160px;
}
.boildetail-head-bg img {
  width: 100%;
  height: 160px;
}
.boildetail-head-bg .box-shadow-bg {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 160px;
  box-shadow: inset 15px 160px 5px rgba(0,0,0,.4);
}
.boildetail .boildetail-head {
    z-index: 1510;
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    top: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    /* background-color: #f5f5f7; */
    /* box-shadow: inset 0px -1px 1px -1px #A7A7AB; */
    color: #000;
}
.boildetail-head .boildetail-Btn-back {
    float: left;
    height: 100%;
    width: 50px;
    text-align: center;
}
.boildetail-Btn-back i {
    transform:rotate(180deg);
    color: #fff;
    font-size: 25px;
    font-weight: 500;
}
.boildetail-fenxiang {
    float: right;
    width: 40px;
    height: 100%;
    color: #fff;
}
.boildetail-fenxiang i {
    font-size: 25px;
    font-weight: 400;
}
.boildetail-author-infor {
    -ms-flex: 1;
        flex: 1;
}
.boildetail-author-infor .author-img {
    float: left;
    width: 40px;
    height: 40px;
    line-height: 40px;
}
.boildetail-author-infor .author-img img{
    display: block;
    width: 30px;
    height: 30px;
    margin-top: 5px;
    border-radius: 50%;
}
.boildetail-author-infor span {
    float: left;
    display: inline-block;
    width: 70px;
    font-size: 15px;
    color: #fff;
}
.attend {
    position: absolute;
    bottom: 20px;
    height: 30px;
    width: 100%;
    /* background: #fff; */
}
.attend .attend-num {
    position: absolute;
    display: inline-block;
    float: left;
    height: 30px;
    width: 120px;
    border-radius: 50px;
    background-color: #FD8306;
    margin-left: 15px;
    color: #fff;
    font-size: 14px;
}
.attend .attend-num span {
    position: absolute;
    height: 30px;
    width: 100px;
    line-height: 30px;
    text-align: right;
    color: #fff;
}
.attend .attend-num span i {
    padding-right: 8px;
}
.attend-img-container {
    height: 100%;
    width: 350px;
}
.attend ul .attend-img {
    float: right;
    display: inline-block;
    height: 30px;
    width: 30px;
    border-radius: 50%;
    margin-left: -12px;
}
.attend ul .attend-img  img{
    display: block;
    height: 30px;
    width: 30px;
    border-radius: 50%;
}
.boildetail-main {
    padding: 20px 15px;
    background: #fff;
}
.boildetail-main .boildetail-title {
    color: #000;
    font-size: 18px;
    line-height: 22px;
    font-weight: 600;
    padding-bottom: 15px;
}
.boildetail-main .boildetail-content {
    font-size: 14px;
    line-height: 16px;
    padding-bottom: 15px;
}
.boildetail-main-ic span {
    font-size: 12px;
    color: #D5D1D1;
    padding-right: 15px;
}
.discuss {
    margin-top: 10px;
    background: #fff;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.discuss .discuss-head {
    height: 40px;
    line-height: 40px;
    width: 100%;
    box-shadow: inset 0px -1px 1px -1px #A7A7AB;
}
.discuss .discuss-head .left {
    float: left;
    margin-left: 15px;
    font-size: 15px;
}
.discuss .discuss-head .zui {
    float: right;
    margin-right: 15px;
}
.discuss-section {
    padding: 20px 15px 15px;
}
.discuss-list .discuss-list-head {
    height: 40px;
}
.discuss-list .discuss-list-head .profile-img {
    float: left;
    height: 40px;
    width: 40px;
}
.discuss-list .discuss-list-head .profile-img img {
    display: block;
    width: 100%;
    height: 100%;
}
.discuss-list .discuss-list-head .profile-name {
    float: left;
    margin-left: 10px;
    height: 40px;
}
.discuss-list-head .profile-name p {
    color: #0A0E7E;
    line-height: 22px;
}
.discuss-list-head .profile-name span {
    font-size: 14px;
    color: #D5D1D1;
}
.discuss-list-head .praise {
    float: right;
    height: 40px;
    line-height: 40px;
    width: 60px;
    background: #E2F4F7;
    color: #1DA6EB;
    text-align: center;
}
.discuss-list-head .praise i {
    padding-right: 5px;
}
.discuss-list .discuss-main {
    padding: 10px 0px 15px 50px;
    color: #000;
    line-height: 20px;
}
.discuss-list .discuss-fx {
    padding-left: 50px;
}
.discuss-list .discuss-fx i {
    font-size: 20px;
    padding-right: 40px;
}
.discuss-input {
    z-index: 1510;
    position: fixed;
    display: -ms-flexbox;
    display: flex;
    bottom: 0;
    background: #fff;
    width: 100%;
    height: 50px;
    line-height: 50px;
    box-shadow: inset 0px 1px 1px -1px #A7A7AB;
}
.discuss-input .discuss-input-ic {
    float: left;
    height: 100%;
    width: 50px;
    text-align: center;
}
.discuss-input .discuss-input-ic i {
    color: #ACA9A9;
    font-size: 20px;
}
.discuss-input input {
    -ms-flex: 1;
        flex: 1;
    height: 36px;
    margin-top: 7px;
    border: 0.5px solid #DEDCDC;boildetail-main
    border-radius: 5px;
}
.discuss-input span {
    display: block;
    float: right;
    width: 50px;
    text-align: center;
    color: #2375E3;
}



